@media (max-width: 768px) {
    .event-register{
        background-color: #f0f0f0;
        padding-top: 60px;
        position: relative;

        .event-confirm-mobile{
            bottom: 60px;
            padding: 0 16px;
            position: fixed;
            width: 100%;
        }
    }

    .mobile-header {
        display: flex;
        height: 38px;
        padding: 0 20px;
        position: absolute;
        top: 20px;
        width: 100vw;

        .arrow-back-transparent{
            background-color: initial;
            cursor: pointer;
            color: black;
        }

        .title {
            display: inline-block;
            font-size: 1.125rem;
            font-weight: 400;
            padding-top: 2px;
        }

    }

    .event-details {
        padding: 16px;

        .event-wrapper{
            .event-reg-card {
                margin: auto;
                max-width: calc(100vw-32px);
                width: calc(100vw-32px);
                border-radius: 12px;
                color: white;
                height: auto;
                position: relative;
                overflow: hidden;
                width: 343px;
                max-width: 343px;
            }
        }

        .event-reg-card-wrapper {
            background-color: #38539a;
            padding: 16px;

            .event-reg-card-title{
                font-size: 1rem;
                font-weight: 700;
            }

            .event-reg-card-toggle{
                cursor: pointer;
                position: absolute;
                right: 16px;
                top: 16px;
                color:white;
            }

            .event-item{
                .event-reg-card-language{
                    font-size: .875rem;
                    font-weight: 400;
                }
                .event-reg-card-item{
                    font-size: .875rem;
                    font-weight: 400;
                    padding-top: 16px;
                    vertical-align: top;
    
                    .icon{
                        display: inline-block;
                        vertical-align: top;
                        width:30px;
                    }
    
                    .info{
                        display: inline-block;
                        font-weight: 400;
                    }
                }   
                
            }

            .ticket{
                .price{
                    display: inline-block;
                    width: 50%;
                    font-size: .875rem;
                }
            }      
            
        }

    }

    

    .amway-button {
        background-color: black;
        border: 1px solid black;
        border-radius: 100px;
        color: white;
        cursor: pointer;
        font-size: .875rem;
        font-weight: 400;
        height: 100%;
        line-height: 30px;
        position: relative;
        text-align: center;
        width: 100%;
    }
}



@media (min-width: 768px) {
    .mobile-header {
        display: none;

        .arrow-back {
            cursor: pointer;
        }
    }

    .event-confirm {}
}